  /*Мой спиннер*/
.spinner{
  position: fixed; 
  top: 38%;
  left: 25%;
  right: 25%;
  bottom: 38%;
  width:10em;
  height:10em;
  border-top:1em solid #0077ff;
  border-right:1em solid transparent;
  border-radius:50%;
  margin:auto ;
  animation:spinner 1.25s linear infinite;
  z-index: 9999; 
}
.circle{
    width:1em;
    height:1em;
    background-color:#0077ff;
    border-radius:50%;
    margin-left:8.5em;
    margin-top:0.5em;
}
@keyframes spinner{
    100% {
        transform: rotate(360deg);
        transition:0.4s all ease;
    }
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); 
  z-index: 9998; 
}           /*Мой спиннер*/

