  text-align: center;
  background: var(--text-primary, #333);
  color: var(--background-card, #cfcfcf);
  z-index: 1001;
  animation: slideAndShake 5s ease-in-out forwards;
}

@keyframes slideAndShake {
  0% {
    top: -100px; 
    opacity: 0;
  }
  10% {
    top: 24px; 
    opacity: 1;
  }
  20%, 80% {
    top: 20px;
  }
  90% {
    top: 24px; 
    opacity: 1;
  }
  100% {
    top: -100px;  
    opacity: 0; 
  }
}
